/*********************************header*************************************/


.page-header {
	position: relative;
	margin-bottom: 20px;
}

.page-header__holder {
	height: 142px;
	background-color: crimson;
}

.page-header__wrapper {
	width: 100%;
	max-width: 1280px;
	margin: 0 auto;
	padding-right: 15px;
	padding-left: 15px;
}

.top-nav {
	position: absolute;
	z-index: 1000;
	top: 0; 
	min-height: 56px;
	min-width: 280px;
	display: flex;
	flex-direction: column;
	background-color: #666666;
	border-bottom: 5px solid #ffaa4b;
}

.top-nav ul {
	margin: 0;
	padding: 5px 0;
	list-style: none;
}

.top-nav li {
	display: block;
}

.top-nav__topmenu a {
	display: block;
	padding: 10px;
	font-size: 20px;
	color: #000000;
	text-shadow: 0 0 20px #999999;
}

.lang-switch {
	padding: 8px 0;
	border-top: 1px solid #ffaa4b;
	border-bottom: 1px solid #ffaa4b;
	text-align: center;
}

.top-nav__user {
	border-bottom: 1px solid #ffaa4b;
}

.top-nav__user .current {
	background: #71caf2;
	transition-duration: 300ms; 
}

.top-nav__user a:active {
	background: rgba(0, 0, 0, .1);
	transition-duration: 300ms; 
}

.top-nav__logo {
	overflow: hidden;
	padding: 15px 10px;
	order: -1;
}

.top-nav__name {
	height: 36px;
	font-weight: 500;
	font-size: 28px;
	letter-spacing: 1px;
	color: #FFAA4B;
}

.top-nav__name h1 {
	margin: 0;
}

.top-nav__name span {
	display: none;
}

.top-nav__list {
	margin: 0;
	padding: 0;
	list-style: none;
}

.top-nav__list a {
	display: block;
	padding: 10px;
	font-size: 20px;
	color: #000000;
	text-shadow: 0 0 20px #999999;
}

.top-nav__list a:active {
	background: rgba(0, 0, 0, .1);
	transition-duration: 300ms; 
}

.top-nav__list .current {
	background: #71caf2;
	transition-duration: 300ms; 
}

.lang-switch a {
	display: inline-block;
	width: 40px;
	font-size: 18px;
	text-transform: uppercase;
	color: #000000;
	background: rgba(256,256,256,.25);
	border: 1px solid #ffaa4b;
	border-radius: 2px;
	margin: 3px;
	padding: 3px 0;
	text-align: center;
}

.header--closed {
	& .top-nav__logo {
		order: 10;
	}
	& li:not(.current),
	& .lang-switch {
		display: none;
	}
	& .top-nav__user {
		border: none;
	}
	& .top-nav {
		width: 100%;
	}
	& ul {
		padding: 1px 0 1px 50px;
	}
}

