}

.lastest-news__item {
	display: block;
	position: relative;
	margin-bottom: 30px;
	box-shadow: inset 0 0 50px #dadada, 0 8px 0 #cccccc;
	padding: 8px;
	transition: 300ms;
}

.lastest-news__item:hover {
	box-shadow: inset 0 0 50px #dadada, 0 8px 0 #cccccc, 0 8px 15px #ed8114;
	transform: translateY(-4px);
	transition: 300ms;
}

.lastest-news__link {
